import { reactShinyInput } from 'reactR';
import { Container } from 'reactstrap';
import { useState, useEffect } from 'react';

function ItemsContainerUI({ configuration, value, setValue }) {
  const choices = configuration.choices
  console.log('con-choices')
  const [leftItems, setLeftItems] = useState([
    { id: 1, text: 'Item 1', selected: false },
    { id: 2, text: 'Item 2', selected: false },
    { id: 3, text: 'Item 3', selected: false },
    { id: 4, text: 'Item 4', selected: false },
    { id: 5, text: 'Item 5', selected: false },
  ]);
  const [rightTopItems, setRightTopItems] = useState([]);
  const [rightBottomItems, setRightBottomItems] = useState([]);
  const [selectedRegion, setSelectedRegion] = useState('left'); // 初始化默认为左边区域
  const [IsMoved, setIsMoved] = useState(true);
 
  const moverighttopItems = () => {
    if (selectedRegion === 'left') {
      const selectedItems = leftItems.filter((item) => item.selected);
      setLeftItems(leftItems.filter((item) => !item.selected));
      /*>setValue(leftItems.filter((item) => !item.selected));*/
      setRightTopItems((prevItems) => [...prevItems, ...selectedItems].map((item) =>
      selectedItems.includes(item) ? { ...item, selected: false } : item
    ));
    } else if (selectedRegion === 'rightTop') {
      const selectedItems = rightTopItems.filter((item) => item.selected);
      setRightTopItems(rightTopItems.filter((item) => !item.selected));
      setLeftItems((prevItems) => [...prevItems, ...selectedItems].map((item) =>
      selectedItems.includes(item) ? { ...item, selected: false } : item
    ));
    
    }
/*const updateItems = [{left: leftItems, rightop: rightTopItems}]*/
    setIsMoved(true);
  };
  useEffect(() => {
    if(IsMoved){
      setValue({left: [...leftItems].map((item) => item.text), rightop: [...rightTopItems].map((item) => item.text)});
      setIsMoved(false);
    }
    
  }, [leftItems, rightTopItems, IsMoved ])

  const moverightdownItems = () => {
    if (selectedRegion === 'left') {
      const selectedItems = leftItems.filter((item) => item.selected);
      setLeftItems(leftItems.filter((item) => !item.selected));
      setRightBottomItems((prevItems) => [...prevItems, ...selectedItems].map((item) =>
      selectedItems.includes(item) ? { ...item, selected: false } : item
    ));
    } else if (selectedRegion === 'rightBottom') {
      const selectedItems = rightBottomItems.filter((item) => item.selected);
      setRightBottomItems(rightBottomItems.filter((item) => !item.selected));
      setLeftItems((prevItems) => [...prevItems, ...selectedItems].map((item) =>
      selectedItems.includes(item) ? { ...item, selected: false } : item
    ));
    }
  };

  const toggleSelection = (itemId) => {
    setLeftItems((prevItems) =>
      prevItems.map((item) =>
        item.id === itemId ? { ...item, selected: !item.selected } : item
      )
    );
    setSelectedRegion('left');
  };

  const toggleSelectionRightTop = (itemId) => {
    setRightTopItems((prevItems) =>
      prevItems.map((item) =>
        item.id === itemId ? { ...item, selected: !item.selected } : item
      )
    );
    setSelectedRegion('rightTop');
  };

  const toggleSelectionRightBottom = (itemId) => {
    setRightBottomItems((prevItems) =>
      prevItems.map((item) =>
        item.id === itemId ? { ...item, selected: !item.selected } : item
      )
    );
    setSelectedRegion('rightBottom');
  };

  return (
    <div className="container">
      <div className="left">
        <h2>左边区域</h2>
        <ul>
          {leftItems.map((item) => (
            <li
              key={item.id}
              className={item.selected ? 'selected' : ''}
              onClick={() => toggleSelection(item.id)}
            >
              {item.text}
            </li>
          ))}
        </ul>
      </div>
      <div className="center">
        <div className="buttons">
          <button onClick={moverighttopItems}>
            {selectedRegion === 'rightTop' ? <span>&#8592;</span> : <span>&#8594;</span>} 
          </button>
          <h2>-----</h2>
          <button onClick={moverightdownItems}>
            {selectedRegion === 'rightBottom' ? <span>&#8592;</span> : <span>&#8594;</span>}
          </button>
        </div>
      </div>
      <div className="right">
      <div className="right-top">
        <h2>右上区域</h2>
        <ul>
          {rightTopItems.map((item) => (
            <li
              key={item.id}
              className={item.selected ? 'selected' : ''}
              onClick={() => toggleSelectionRightTop(item.id)}
            >
              {item.text}
            </li>
          ))}
        </ul>
      </div>
      <div className="right-down">
        <h2>右下区域</h2>
        <ul>
          {rightBottomItems.map((item) => (
            <li
              key={item.id}
              className={item.selected ? 'selected' : ''}
              onClick={() => toggleSelectionRightBottom(item.id)}
            >
              {item.text}
            </li>
          ))}
        </ul>
      </div>
      </div>
    </div>
  );
}

export default function initItemsContainer(){
  reactShinyInput(
    '.items_container',
    'reactstrapTest.items_container',
    ItemsContainerUI
  );
}

